.App {
  text-align: center;
}

body{
  background-color: #feffff;
}

.App-header {
  background-color: #feffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#outlined-basic{
  height: 2px;
  font-size: 1px;
}



.alerta{
  display: block;
  width: auto;
  height: auto;
  margin-left: 56px;
  margin-right: 56px;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 15px;
  border-radius: 2px;

}

.alerta-ok{
  background-color: rgba(16, 53, 30, 0.192);
  color: rgb(16, 53, 30);
}


.alerta-error{
  background-color: rgba(83, 22, 43, 0.192);
  color: rgb(83, 22, 43);
}

/*navbar*/
  #appbar-nav{
    background-color: #feffff;
    box-shadow: none;
  }  

  #nav-options{
    align-items: flex-end;
  }
      

@media screen and (max-width:991px)
{
  #nav-elements{
    display: none;
    }  
    
    #nav-elements-res{
      color: #1493f6;
      text-decoration: none;
      font-weight: 600;
      font-size: 11px;
      align-items: center;
      width: 120px;
    }
    
  #menu-icon{
    color: #1493f6;
  }

  #searching{
    display: none;
  }

  #home-nav{
    display: none;
  }
}

@media screen and (min-width:991px)
{
  nav{
    display: block;
    box-shadow: none;
  }

  #nav-elements{
    color: #1493f6;
    text-decoration: none;
    font-weight: 600;
    font-size: 11px;
    align-items: center;
    width: 120px;
  }

  #icons-elements{
    color: #1493f6;
    width: 50px;
  }

  #menu-icon{
    display: none;
  }
  
  
  #home-nav{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    color: #1493f6;
    font-weight: 200;
    margin-left: 100px;
  }
}

/*signin*/

@media screen and (max-width:991px)
{.signin-form{
  margin-top: 10px;
  width: 520px;
  height: 600px;
  margin-right: 10%;
  margin-left: 10%;
  border-radius: 2%;
  display: inline-block;
  align-items: center;
  justify-content: center;
  background-color: #feffff;
 color: #383531;
 box-shadow: #81818160 6px 10px 10px;
}

.logo-sign-form{
  margin-top: 2px;
  width: 500px;
  height: 480px;
  margin-left: 10%;
  border-radius: 2%;
  display: inline-block;
  align-items: center;
  justify-content: center;
  background-color: #feffff;
 color: #383531;
}

.logo-signin{
  display: none;
}

#outlined-basic {
  height: 2px;
  font-size: 10px;
}

.logo-signin-res{
  margin-top: 10px;
  background-color: none;
  height: 100px;
  width: 100px;
}

}

@media screen and (min-width:991px)
{

  .logo-signin-res{
    display: none;
  }
  
  .signin-form{
  margin-top: 10px;
  width: 520px;
  height: 500px;
  margin-right: 10%;
  margin-left: 10%;
  border-radius: 2%;
  display: block;
  align-items: flex-end;
  justify-content: flex-end;
  background-color: #feffff;
 color: #383531;
 box-shadow: #81818160 6px 10px 10px;
}

.logo-sign-form{
  margin-top: 2px;
  width: 500px;
  height: 480px;
  margin-left: 10%;
  border-radius: 2%;
  display: block;
  align-items: flex-start;
  justify-content: flex-start;
  background-color: #feffff;
 color: #383531;
}

#outlined-basic {
  height: 2px;
  font-size: 15px;
}

.logo-signin{
  margin-top: -10px;
  background-color: none;
  height: 100%;
  width: 400px;
}

}


.signin-form h6{
  color: #1493f6;
  font-size: 25px;
  font-weight: bolder;
  margin-top: 10px;
}

.formulario-signin{
  margin-bottom: 40px;
}


.signin-header {
  background-color: #feffff;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  font-size: calc(10px + 2vmin);
  color: white;
}

.signin-div{
  width: 500px;
  height: 48px;
  margin-top: 4px;
}

#boton-signin{
  margin-top: 20px;
  width: 200px;
}

/*login*/

.login-div{
  width: 400px;
  height: 40px;
  margin-top: 15px;
}

.login-grid{
  margin-top: 60px;
}

.login-form{
  margin-top: 50px;
  width: 400px;
  height: 430px;
  border-radius: 10%;
  align-items: center;
  justify-content: center;
  background-color: #feffff;
 color: #383531;
 box-shadow: #81818160 6px 10px 10px;
}


.field-usuario{
  margin-top: 1px;
  width: 300px;
  font-size: 30px;
}

.boton-login{
  margin-top: 3px;
  width: 200px;

}

#enlace-cuenta{
  color: #3f51b5;
  text-decoration: none;

}

/*Home*/
.item-home{
  width: 140px;
  height: 200px;
  margin: 5px;
  border-radius: 5px;
  text-decoration: none;
  text-align: center;
  background-color: antiquewhite;
}

.home1{
  background-color: rgb(89, 114, 168);

}

.home-header{
  display: flex;
  align-items: center;
  align-content: center;
  width: 1234px;
  height: 100px;
}

.home-header h6{
  color: #1493f6;
  align-items: center;
  align-content: center;
  font-size: 20px;
  margin-left: 50%;
}

.grid{
  margin-left: 50px;
  margin-right: 50px;
}

/*Noticias & Anuncios*/
.news{
  margin-left: 70px;
  margin-right: 70px;
}

#new-img{
  height: 98%;
  width: 100%;
  border-radius: 20px;
}

#typo-news{
  color: #1493f6;
}

#news-top{
  margin-left: 10px;
  margin-right: 100px;
  align-items: center;
  align-content: center;
}

.news-section-top{
  position: relative;
  display: inline-block;
  text-align: center;
  margin-right: 40px;
  margin-bottom: 20px;
}

#new-img-top{
  height: 85%;
  width: 100%;
  border-radius: 20px 20px 0px 0px;
}

.text-top{
  position: absolute;
  bottom: 10px;
  left: 12%;
  right: 12%;
  font-size: 60%;
  color: #feffff;
  text-shadow: 1px 0 black;

}

#news-ad{
  margin-left: 50px;
  margin-right: 50px;
  align-items: center;
  align-content: center;
}

.news-section-ad{
  position: relative;
  display: inline-block;
  text-align: center;
  margin-left: 40px;
  margin-bottom: 20px;
}

#new-img-ad{
  height: 85%;
  width: 85%;
  border-radius: 20px;
}

/*Footer*/

#appbar-footer{
  background-color: #1493f6;
  box-shadow: none;
}  
  
#home-footer{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  color: #ffffff;
  font-weight: 200;
}
#footer-elements{
  color: #ffffff;
  text-decoration: none;
  font-size: 10px;
}

#footer-icons-elements{
  color: #ffffff;
  width: 50px;
}

#pagination{
  align-items: center;
  align-content: center;
  margin-top: 10px;
  margin-bottom: 50px;
  margin-left: 10%;
  margin-right: 10%;
}

#appbar-footer-list{
  box-shadow: none;
  background-color: #ffffff;
  padding-bottom: 20px;
}
#appbar-footer-list ul{
  color: #303030;
}  
#appbar-footer-list li{
  color: #616161;
}  
#appbar-footer-list a{
  color: #616161;
  text-decoration: none;
}  
